import _ from "lodash";
import React, { useEffect } from "react";
import MovieList from "./MovieList";
import "animate.css";
import pagi from "../../utils/paginate";
import Sidebar from "../common/Sidebar";
import Pagination from "../common/pagination";
import { useSelector, useDispatch } from "react-redux";
import {
	useSearchParams,
	useNavigate,
	useOutletContext,
} from "react-router-dom";
import { addMovie, setCurrentPage, setSortBy } from "../../redux/movieSlice";
import {
	setMovieAdded,
	setEditingFalse,
	setMovieSubmitted,
	resetPreviousMovieData,
} from "../../redux/addMovieSlice";

const DisplayMovies = ({ texxt }) => {
	const [queryParams] = useSearchParams();
	useEffect(() => {
		if (editing) {
			dispatch(setEditingFalse());
		}
	}, []);

	const navigate = useNavigate();
	const [passedClass, moviesDisplayAnimation] = useOutletContext();
	// console.log(Object.fromEntries([...queryParams]));

	// VARIABLES:: GET CURRENT PAGE AND PER PAGE SIZE FROM REDUX STORE
	const addMovieState = useSelector(state => state.addMovieReducer);

	const { movie_added, new_movie, editing } = addMovieState;

	const { movies, currentPage, perPage, selectedGenre, sortBy } = useSelector(
		state => state.moviesReducer
	);

	const { path, order } = sortBy;
	var items = undefined; //VALUE SET LATER WHEN ITEMS ARE FILTERED

	// FUNCTIONS
	const dispatch = useDispatch();
	const paginate = m => pagi(m, currentPage, perPage);

	const sorted = moviesToSort => _.orderBy(moviesToSort, [path], [order]);

	const filterAndSortMovies = (movies_, FilterGenre) => {
		// could check like _id ?  if it exists or not if I had not opted for a seperate genre
		if (FilterGenre.name === "All Movies") {
			items = sorted(movies_);
			return paginate(items);
		} else {
			items = movies_.filter(movie => movie.genre._id === FilterGenre._id);
			const sorted_items = sorted(items);
			return paginate(sorted_items);
		}
	};

	const filteredMovies = filterAndSortMovies(movies, selectedGenre);

	// HANDLER FUNCATIONS
	const pageHandler = currentSelectedPage => {
		dispatch(setCurrentPage(currentSelectedPage));
	};

	const sortHandler = sortByNew => {
		dispatch(setSortBy(sortByNew));
	};

	// if you don't reset the movie added, it gives infinite re-renders as everytime you add a movie, it will re-render the page and movie added will be true
	movie_added &&
		dispatch(setMovieAdded({ movie_added: false })) &&
		dispatch(setMovieSubmitted({ movie_submitted: false })) &&
		dispatch(resetPreviousMovieData()) &&
		dispatch(addMovie(new_movie));

	// RETURN
	return (
		<div className="main-grid mt-5">
			<div className="item1">
				<Sidebar />
			</div>
			<div className="item2">
				<button
					className="new-movie btn btn-primary"
					onClick={() => navigate("add")}
				>
					New Movie
				</button>
				<h1> Movies {texxt}</h1>
				<div className={"tableAndPages " + moviesDisplayAnimation}>
					<MovieList
						movies={filteredMovies}
						totalMovies={items}
						sortBy={sortBy}
						onSort={sortHandler}
					/>
					<Pagination
						items={items.length}
						perPage={perPage}
						currentPage={currentPage}
						onPageChange={pageHandler}
					/>
				</div>
			</div>
		</div>
	);
};

export default DisplayMovies;
