import React, { useEffect } from "react";
import { useParams, usePrompt } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import EditMovieForm from "./EditMovieForm";
import Redirect from "../common/Redirect";
import { setEditingTrue } from "../../redux/addMovieSlice";

const MovieDetails = () => {
	const { id } = useParams();

	// set editing to true
	const dispatch = useDispatch();
	dispatch(setEditingTrue());

	// check if id in pool
	const inPool = useSelector(state =>
		state.moviesReducer.movies.some(movie => movie._id === id)
	);

	// if not in pool, redirect to 404
	const movieFound = useSelector(state => {
		// check if id in pool then return movie
		if (inPool) {
			return state.moviesReducer.movies.find(movie => movie._id === id);
		}
		return {};
	});

	const { _id, title, genre, numberInStock, dailyRentalRate } = movieFound;

	return inPool ? (
		<div>
			<h1>Movie id is {id}</h1>
			<EditMovieForm
				_id={_id}
				title={title}
				genre={genre.name}
				numberInStock={numberInStock}
				dailyRentalRate={dailyRentalRate}
			/>
		</div>
	) : (
		<Redirect to="/404" />
	);
};

export default MovieDetails;
