import React from "react";
import _ from "lodash";
import PropTypes from "prop-types";
import { useOutletContext } from "react-router-dom";

const Pagination = (props) => {
	const [passedClass, moviesDisplayAnimation, paginationClass] =
		useOutletContext();
	const { items, perPage, currentPage, onPageChange } = props;

	//+1 because until that number, array of numbers for page number tiles
	const noOfPages = Math.ceil(items / perPage);
	// range to iterate over and construct the page number tiles
	const pages = _.range(1, noOfPages + 1);

	return pages.length === 1 ? null : (
		<div className={"" + paginationClass}>
			<nav aria-label="Page navigation example">
				<ul className="pagination d-flex justify-content-center align-items-center">
					{pages.map((page) => (
						<li
							key={page}
							className={
								page === currentPage ? "page-item active" : "page-item"
							}
						>
							<button className="page-link" onClick={() => onPageChange(page)}>
								{page}
							</button>
						</li>
					))}
				</ul>
			</nav>
		</div>
	);
};

Pagination.propTypes = {
	items: PropTypes.number.isRequired,
	perPage: PropTypes.number.isRequired,
	currentPage: PropTypes.number.isRequired,
	onPageChange: PropTypes.func.isRequired,
};

export default Pagination;
