export default function renderSelectOptionInputGroup(
	name,
	options,
	defaultValue,
	SelectOptionInputGroup,
	onOptionChange,
	{ dispatch, setData, setSubmitDisabled }
) {
	return (
		<SelectOptionInputGroup
			name={name}
			options={options}
			defaultValue={defaultValue}
			onChange={({ target: { value } }) =>
				onOptionChange({ name, value }, dispatch, setData, setSubmitDisabled)
			}
		/>
	);
}
