export default function validate(movie_state, Joi, schema) {
	const errors = {};
	const { data } = movie_state;

	//#region [rgba (55, 100, 100, 0.2)] validate data using joi
	var { error } = Joi.validate(data, schema, {
		abortEarly: false,
	});

	// if null, return null
	// if not null, then there is an error
	if (!error) return null;

	for (let i of error.details) errors[i.path[0]] = i.message;
	return errors;
	//#endregion
}
