import React from "react";
import { Outlet } from "react-router-dom";

import Navbar from "./common/Navbar";

export default function BaseApp() {
	const passedClass = "";
	// animate__animated animate__fadeIn
	const moviesDisplayAnimation = "";
	// "animate__animated animate__headShake animate__delay-1s";
	// "animate__animated animate__hinge";
	const paginationClass = "";
	// "animate__animated animate__rubberBand animate__delay-1s";

	return (
		<React.Fragment>
			<Navbar />
			<div className="container baseApp ">
				<Outlet
					context={[passedClass, moviesDisplayAnimation, paginationClass]}
				/>
			</div>
		</React.Fragment>
	);
}
