import React from "react";
import {
	toggleFavourite,
	removeMovieAndResetIfNeeded,
} from "../../redux/movieSlice";
import { Link } from "react-router-dom";

const TableBody = ({ movies, dispatch, moviesCount }) => {
	const favourite = (id) => {
		dispatch(toggleFavourite(id));
	};

	return (
		<tbody>
			{movies.map((movie) => (
				<tr key={movie._id}>
					<td className="titles">
						<Link to={`/movies/${movie._id}`}>{movie.title}</Link>
					</td>
					<td>{movie.genre.name}</td>
					<td>{movie.numberInStock}</td>
					<td>{movie.dailyRentalRate}</td>
					<td>
						{
							<i
								style={{ cursor: "pointer" }}
								className={
									movie.isFavourite ? "fa fa-heart btn" : "fa fa-heart-o btn"
								}
								onClick={() => favourite(movie._id)}
								aria-hidden="true"
							></i>
						}
					</td>
					<td>
						<button
							className="btn btn-danger btn-sm delBtn"
							onClick={() => {
								dispatch(
									removeMovieAndResetIfNeeded({ id: movie._id, moviesCount })
								);
							}}
						>
							Delete
						</button>
					</td>
				</tr>
			))}
		</tbody>
	);
};

export default TableBody;
