// import { Checkmark } from "react-checkmark";

import SuccessAlert from "./SuccessAlert";
import DangerAlert from "./DangerAlert";

const InputGroup = ({ name, labelValue, error,autoFocus, ...rest }) => {
	return (
		<div className="form-group ">
			<label htmlFor={name}>
				{labelValue.charAt(0).toUpperCase() + labelValue.slice(1)}
			</label>
			<br />
			<input
				autoFocus={autoFocus}
				id={name}
				name={name}
				//use ...rest for properties that have same key and value
				// type={type}
				// defaultValue={defaultValue}
				// onChange={onChange}
				{...rest}
				className="form-control"
				placeholder={labelValue.charAt(0).toUpperCase() + labelValue.slice(1)}
				// ref={ref}
			/>
			<br />
			{error && <DangerAlert error={error} />}
			{!error && rest.defaultValue !== "" && (
				<SuccessAlert label={labelValue} />
			)}
		</div>
	);
};

export default InputGroup;
