export default function renderInput(
	name,
	label,
	type = "text",
	autoFocus,
	movie_state,
	InputGroup,
	onChange,
	{
		validateProperty,
		dispatch,
		setError,
		deleteError,
		setData,
		setSubmitDisabled,
		schema,
		Joi
	}
) {
	const { data } = movie_state;

	return (
		<div>
			<InputGroup
				name={name}
				type={type}
				labelValue={label}
				autoFocus={autoFocus}
				defaultValue={data[name]}
				onChange={(e) =>
					onChange(
						e.currentTarget,
						validateProperty,
						dispatch,
						setError,
						deleteError,
						setData,
						setSubmitDisabled,
						schema,
						Joi
					)
				}
				error={movie_state.errors[name]}
				// ref={this.username}
			/>
			{name === "email" ? (
				<small id="emailHelp" className="form-text text-muted">
					We'll never share your email with anyone else.
				</small>
			) : null}
		</div>
	);
}
