import Joi from "joi-browser";
import { nanoid } from "nanoid";
import { useSelector, useDispatch } from "react-redux";
import React from "react";
import { Navigate } from "react-router-dom";
import InputGroup from "../common/InputGroup";
import SelectOptionInputGroup from "../common/SelectOptionInputGroup";
import {
	setError,
	deleteError,
	setSubmitDisabled,
	setMovieSubmitted,
	setMovieAdded,
	storeNewMovie,
	setData,
} from "../../redux/addMovieSlice";
import MovieForm, {
	validate,
	validateProperty,
	onChange,
	onOptionChange,
	renderInput,
	renderSelectOptionInputGroup,
	renderButton,
	handleSubmit,
	doSubmit,
} from "./MovieForm";
import { getGenresListExceptFirst } from "../../services/GenreService";

const AddMovieForm = ({ titlee, genree, numberInStocke, dailyRentalRatee }) => {
	// initialize this variable frist
	const genres = getGenresListExceptFirst();
	const schema = {
		title: Joi.string().required().label("Title"),
		genre: Joi.string().required().label("Genre"),
		numberInStock: Joi.number()
			.positive()
			.integer()
			.required()
			.label("NumberInStock"),
		dailyRentalRate: Joi.number()
			.positive()
			.required()
			.label("DailyRentalRate"),
	};

	const dispatch = useDispatch();
	const movie_state = useSelector((state) => state.addMovieReducer);

	const { movie_submitted } = movie_state;

	// PROPS DRILLING
	const onChangeOptions = {
		validateProperty,
		dispatch,
		setError,
		deleteError,
		setData,
		setSubmitDisabled,
		Joi,
		schema,
	};

	const onOptionChangeOptions = {
		dispatch,
		setData,
		setSubmitDisabled,
	};

	const onSubmitOptions = {
		validate,
		dispatch,
		setError,
		doSubmit,
		movie_state,
		Joi,
		schema,
		storeNewMovie,
		setMovieAdded,
		setMovieSubmitted,
		nanoid,
	};

	const children = (
		<div className="container">
			<div className="row">
				<div className="col-md-6 offset-md-0">
					{movie_submitted && (
						<Navigate
							to="/movies"
							// state={(title, genre, numberInStock, dailyRentalRate)}
							replace={true}
						/>
					)}
					<form onSubmit={(e) => handleSubmit(e, onSubmitOptions)}>
						<h1>Add a movie here</h1>
						{renderInput(
							"title",
							"Title",
							"text",
							true,
							movie_state,
							InputGroup,
							onChange,
							onChangeOptions
						)}
						{renderInput(
							"numberInStock",
							"Number in Stock",
							"number",
							false,
							movie_state,
							InputGroup,
							onChange,
							onChangeOptions
						)}
						{renderInput(
							"dailyRentalRate",
							"Rate",
							"number",
							false,
							movie_state,
							InputGroup,
							onChange,
							onChangeOptions
						)}
						{renderSelectOptionInputGroup(
							"genre",
							genres,
							movie_state.defaultGenre,
							SelectOptionInputGroup,
							onOptionChange,
							onOptionChangeOptions
						)}
						{renderButton("Add Movie", validate, movie_state, Joi, schema)}
					</form>
				</div>
			</div>
		</div>
	);

	return (
		<MovieForm
			children={children}
			schema={schema}
			dispatch={dispatch}
			movie_state={movie_state}
		/>
	);
};

export default AddMovieForm;
