import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { setSelectedGenre, setCurrentPage } from "../../redux/movieSlice";

const Sidebar = (props) => {
	const dispatch = useDispatch();

	var genres = useSelector((state) => state.moviesReducer.genres);
	var selectedGenre = useSelector((state) => state.moviesReducer.selectedGenre);

	const onGenreChange = (SelectedGenre) => {
		dispatch(setSelectedGenre(SelectedGenre));
		dispatch(setCurrentPage(1));
	};

	return (
		<div className="list-group mt-2">
			{genres.map((genre) => (
				<li
					key={genre._id}
					className={
						genre === selectedGenre
							? "list-group-item active"
							: "list-group-item"
					}
					aria-current="true"
					onClick={() => onGenreChange(genre)}
				>
					{genre.name}
				</li>
			))}
		</div>
	);
};

export default Sidebar;
