export default function handleSubmit(
	e,
	{
		validate,
		dispatch,
		setError,
		doSubmit,
		movie_state,
		Joi,
		schema,
		storeNewMovie,
		setMovieAdded,
		setMovieSubmitted,
		nanoid,
	}
) {
	e.preventDefault();
	const errors = validate(movie_state, Joi, schema);
	if (errors) {
		dispatch(setError(errors));
		return;
	}

	doSubmit(
		dispatch,
		storeNewMovie,
		setMovieAdded,
		setMovieSubmitted,
		nanoid,
		movie_state
	);
}
