import { createSlice } from "@reduxjs/toolkit";
import { getGenresListExceptFirst } from "../services/GenreService";

var genresList = getGenresListExceptFirst();

const addMovieSlice = createSlice({
	name: "addMovies",
	initialState: {
		defaultGenre: "Choose a genre ...",
		genresList: genresList,
		submit_disabled: true,
		movie_submitted: false,
		errors: {},
		data: {
			title: "",
			genre: "",
			numberInStock: "",
			dailyRentalRate: "",
		},
		movie_added: false,
		new_movie: {},
		editing: false,
	},
	reducers: {
		storeNewMovie: (state, action) => {
			return { ...state, new_movie: action.payload };
		},
		setSubmitDisabled: (state, action) => {
			return { ...state, ...action.payload };
		},
		setMovieSubmitted: (state, action) => {
			return { ...state, ...action.payload };
		},
		setMovieAdded: (state, action) => {
			return { ...state, ...action.payload };
		},
		setEditingTrue: (state, action) => {
			return { ...state, editing: true };
		},
		setEditingFalse: (state, action) => {
			return {
				...state,
				data: {
					title: "",
					genre: "",
					numberInStock: "",
					dailyRentalRate: "",
				},
				editing: false,
			};
		},

		resetPreviousMovieData: (state, action) => {
			return {
				...state,
				data: {
					title: "",
					genre: "",
					numberInStock: "",
					dailyRentalRate: "",
				},
			};
		},
		setData: (state, action) => {
			return { ...state, data: { ...state.data, ...action.payload } };
		},
		setError: (state, action) => {
			return { ...state, errors: { ...state.errors, ...action.payload } };
		},
		deleteError: (state, action) => {
			let errors = { ...state.errors };
			delete errors[action.payload];
			return { ...state, errors: errors };
		},
	},
});

export const {
	setData,
	setError,
	setEditingTrue,
	setEditingFalse,
	deleteError,
	storeNewMovie,
	setMovieAdded,
	setSubmitDisabled,
	setMovieSubmitted,
	resetPreviousMovieData,
} = addMovieSlice.actions;

export default addMovieSlice.reducer;
