import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { getMovies } from "../services/MovieService";
import { getGenres } from "../services/GenreService";
import { nanoid } from "nanoid";

var movies = getMovies();
var genres = getGenres();

const movieSlice = createSlice({
	name: "movies",
	initialState: {
		movies: movies,
		genres: genres,
		selectedMovie: null,
		selectedGenre: genres[0],
		perPage: 4,
		currentPage: 1,
		sortBy: { path: "title", order: "asc" },
	},
	reducers: {
		addMovie: (state, action) => {
			const { title, genre, numberInStock, dailyRentalRate } = action.payload;
			const _id = nanoid();
			const newMovie = {
				_id,
				title,
				genre: { _id, name: genre },
				numberInStock: parseInt(numberInStock),
				dailyRentalRate: parseFloat(dailyRentalRate),
				isFavourite: false,
				publishDate: new Date().toISOString(),
			};

			return { ...state, movies: [...state.movies, newMovie] };
		},
		removeMovie: (state, action) => {
			const movieIndex = state.movies.findIndex(
				(movie) => movie._id === action.payload
			);

			state.movies.splice(movieIndex, 1);
		},
		//change the selected page of the pagination if all movies in current page are deleted
		pageEmpty: (state) => {
			if (state.currentPage - 1 !== 0) {
				state.currentPage--;
			} else {
				state.currentPage = 1;
			}
		},
		updateMovie: (state, action) => {
			const { _id, title, genre, numberInStock, dailyRentalRate } =
				action.payload;
			const movie = state.movies.find((m) => m._id === _id);
			const updatedMovie = {
				...movie,
				title,
				genre: { _id, name: genre },
				numberInStock: parseInt(numberInStock),
				dailyRentalRate: parseFloat(dailyRentalRate),
				isFavourite: movie.isFavourite,
				publishDate: new Date().toISOString(),
			};

			// replace movie with _id with updated movie
			return {
				...state,
				movies: state.movies.map((m) => (m._id === _id ? updatedMovie : m)),
			};
		},
		setCurrentPage: (state, action) => {
			state.currentPage = action.payload;
		},
		setSelectedGenre: (state, action) => {
			state.selectedGenre = action.payload;
		},
		toggleFavourite: (state, action) => {
			const movie = state.movies.find((m) => m._id === action.payload);
			const index = state.movies.indexOf(movie);
			state.movies[index].isFavourite = !state.movies[index].isFavourite;
		},
		setSortBy: (state, action) => {
			if (state.sortBy.path === action.payload) {
				state.sortBy.order = state.sortBy.order === "asc" ? "desc" : "asc";
			} else {
				state.sortBy.path = action.payload;
				state.sortBy.order = "asc";
			}
		},
	},
});

export const removeMovieAndResetIfNeeded = (payload) => async (dispatch) => {
	await dispatch(removeMovie(payload.id));
	if (payload.moviesCount === 1) {
		await dispatch(pageEmpty());
	}
};

export const {
	addMovie,
	removeMovie,
	updateMovie,
	setCurrentPage,
	setSelectedGenre,
	toggleFavourite,
	setSortBy,
	pageEmpty,
} = movieSlice.actions;

export default movieSlice.reducer;
