import React, { Fragment } from "react";
import { useDispatch } from "react-redux";
import { pageEmpty } from "../../redux/movieSlice";
import TableHead from "./TableHead";
import TableBody from "./TableBody";

const MovieList = ({ movies, totalMovies, sortBy, onSort }) => {
	const dispatch = useDispatch()

	// GET MOVIES FROM REDUX STORE AND APPLY SELECTOR FUNCTION WHICH PAGINATES THE FETCHED MOVIES

	const renderMovies = () => {
		return (
			<Fragment>
				<table className="table">
					<caption className="tableCaption1 text-center">
						{totalMovies.length === 0 ? (
							<p>There are no movies in database for selected genres.</p>
						) : (
							<p>
								Showing {movies.length} movies in the database. (Total in this
								genre:"
								{totalMovies.length}")
							</p>
						)}
					</caption>
					<colgroup span="5" className="data"></colgroup>
					<colgroup>
						<col className="action"></col>
					</colgroup>
					<TableHead sortBy={sortBy} onSort={onSort} />
					<TableBody
						movies={movies}
						moviesCount={movies.length}
						dispatch={dispatch}
					/>
				</table>
			</Fragment>
		);
	};

	return <main>{renderMovies()}</main>;
};

// DEFAULT PROPS
MovieList.defaultProps = {};

export default MovieList;
