export default function doSubmit(
	dispatch,
	storeNewMovie,
	setMovieAdded,
	setMovieSubmitted,
	nanoid,
	movie_state
) {
	// call the server
	dispatch(storeNewMovie({ _id: nanoid(), ...movie_state.data }));
	dispatch(setMovieAdded({ movie_added: true }));
	dispatch(setMovieSubmitted({ movie_submitted: true }));
	console.log("Submitted");
}
