import React from "react";
import {
	HashRouter as Router,
	Routes,
	Route,
	Navigate,
} from "react-router-dom";
import Posts from "./Posts";
import BaseApp from "./BaseApp";
import NotFound from "./NotFound";
import Login from "./Auth/Login";
import Register from "./Auth/Register";
import Wrapper from "./theme/Wrapper";
import { Provider } from "react-redux";
import { store } from "../redux/store";
import Dashboard from "./admin/Dashboard";
import Products from "./Products/Products";
import Home from "./Home";
import Page from "./common/Page";
import AddMovieForm from "./Movies/AddMovieForm";
import MovieDetails from "./Movies/MovieDetails";
import DisplayMovies from "./Movies/DisplayMovies";
import ProductDetails from "./Products/ProductDetails";
import Redirect from "./common/Redirect";
import styled, { ThemeProvider } from "styled-components";

const theme = {
	primary: "#00bcd4",
	secondary: "#f44336",
	font: "Verdana",
	fontSize: "1.5rem",
};

export default function RouterApp() {
	return (
		<ThemeProvider theme={theme}>
			<Wrapper>
				<Provider store={store}>
					<Router>
						<Routes>
							<Route
								path="/"
								element={<Page title="" children={<BaseApp />} />}
							>
								<Route
									path="home"
									element={<Page title="Home" children={<Home />} />}
								/>
								<Route
									path="admin"
									element={<Page title="Admin" children={<Dashboard />} />}
								/>
								<Route
									path="movies"
									element={
										<Page
											title="Movies Display"
											children={<DisplayMovies texxt="" />}
										/>
									}
								/>
								<Route
									path="movies/add"
									element={
										<Page
											title="Add Movie"
											children={
												<AddMovieForm
													titlee=""
													genree=""
													numberInStocke=""
													dailyRentalRatee=""
												/>
											}
										/>
									}
								/>
								<Route
									path="movies/:id"
									element={
										<Page title="Edit Movie" children={<MovieDetails />} />
									}
								/>
								<Route
									path="products/:id"
									element={
										<Page title="Details View" children={<ProductDetails />} />
									}
								/>
								<Route
									path="products"
									element={
										<Page
											title="All Products"
											children={<Products prop1="hekki" />}
										/>
									}
								/>
								<Route
									path="items"
									element={<Navigate to="/products" replace="false" />}
								/>
								<Route
									path="login"
									element={<Page title="Sign In" children={<Login />} />}
								/>
								<Route
									path="register"
									element={<Page title="Sign Up" children={<Register />} />}
								/>
								<Route
									path="posts"
									element={<Page title="All Post" children={<Posts />} />}
								>
									<Route
										path=":year"
										element={<Page title="" children={<Posts />} />}
									>
										<Route
											path=":month"
											element={<Page title="" children={<Posts />} />}
										>
											<Route
												path=":day"
												element={<Page title="" children={<Posts />} />}
											/>
										</Route>
									</Route>
								</Route>
								<Route
									path="not-found"
									element={<Page title="Not Found" children={<NotFound />} />}
								/>
								<Route path="404" element={<Redirect to="/not-found" />} />
								<Route path="*" element={<Redirect to="/not-found" />} />
							</Route>
						</Routes>
					</Router>
				</Provider>
			</Wrapper>
		</ThemeProvider>
	);
}
