const TableHead = ({ sortBy, onSort }) => {
	const orderIcon = sortBy.order === "asc" ? "" : "";
	return (
		<thead>
			<tr>
				<th
					style={{ cursor: "pointer" }}
					onClick={() => onSort("title")}
					scope="col"
				>
					Title {orderIcon}
				</th>
				<th
					style={{ cursor: "pointer" }}
					onClick={() => onSort("genre.name")}
					scope="col"
				>
					Genre {orderIcon}
				</th>
				<th
					style={{ cursor: "pointer" }}
					onClick={() => onSort("numberInStock")}
					scope="col"
				>
					Stock {orderIcon}
				</th>
				<th
					style={{ cursor: "pointer" }}
					onClick={() => onSort("dailyRentalRate")}
					scope="col"
				>
					Rental Rate {orderIcon}
				</th>
				<th
					style={{ cursor: "pointer" }}
					onClick={() => onSort("isFavourite")}
					scope="col"
				>
					Favourite {orderIcon}
				</th>
				<th scope="col">Action</th>
			</tr>
		</thead>
	);
};

export default TableHead;
