// destructure the event object and rename its properties
export default function onChange(
	currentTarget,
	validateProperty,
	dispatch,
	setError,
	deleteError,
	setData,
	setSubmitDisabled,
	schema,
	Joi
) {
	// check for errors in current field
	const errors = {};
	const errorMessage = validateProperty(currentTarget, schema, Joi);
	if (errorMessage) {
		errors[currentTarget.name] = errorMessage;
		dispatch(setError(errors));
	} else dispatch(deleteError(currentTarget.name));

	// set username and password to the  as they are typed
	const finalData = {};
	finalData[currentTarget.name] = currentTarget.value;
	dispatch(setData(finalData));

	if (errors === {}) dispatch(setSubmitDisabled({ disabled: false }));
}
