import React from "react";
import Form from "../common/Form";
import Joi from "joi-browser";

class Register extends Form {
	// passedClass = "animate__animated animate__bounceInDown animate__faster";
	passedClass = "";

	state = {
		data: {
			email: "",
			username: "",
			password: "",
		},
		errors: {},
		disabled: true,
	};

	schema = {
		email: Joi.string()
			.required()
			.email({
				minDomainSegments: 2,
				tlds: { allow: ["com", "net"] },
			}),
		username: Joi.string()
			.alphanum()
			.min(3)
			.max(30)
			.required()
			.label("Username"),
		password: Joi.string().required().label("Password"),
	};

	handleSubmit = (e) => {
		e.preventDefault();
		const errors = this.validate();
		this.setState({ errors: errors || {} });
		if (errors) return;

		this.doSubmit();
	};

	doSubmit() {
		// call the server
		console.log("Submitted");
	}

	render() {
		return (
			//center the div
			<div className={"container " + this.passedClass}>
				<div className="row">
					<div className="col-md-4 offset-md-4">
						<h1 className="text-center">Register</h1>
						<form onSubmit={this.handleSubmit}>
							{this.renderInput("email", "Email", "email")}
							{this.renderInput("username", "Username")}
							{this.renderInput("password", "Password", "password")}
							<div className="text-center">{this.renderButton("Sign Up")}</div>
						</form>
					</div>
				</div>
			</div>
		);
	}
}

export default Register;
