import Joi from "joi-browser";
import React, { useEffect, useState } from "react";
import { useNavigate, usePrompt } from "react-router-dom";
import InputGroup from "../common/InputGroup";
import { useSelector, useDispatch } from "react-redux";
import SelectOptionInputGroup from "../common/SelectOptionInputGroup";
import { getGenresListExceptFirst } from "../../services/GenreService";
import {
	setError,
	setEditingFalse,
	deleteError,
	setSubmitDisabled,
	resetPreviousMovieData,
	setData,
} from "../../redux/addMovieSlice";
import { updateMovie } from "../../redux/movieSlice";

const EditMovieForm = ({
	_id,
	title,
	genre,
	numberInStock,
	dailyRentalRate,
}) => {
	useEffect(() => {
		dispatch(setData({ title, genre, numberInStock, dailyRentalRate }));
		// if (editing) {
		// 	setIsBlocking(true);
		// }
	}, []);

	// initialize this variable frist
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const movie_state = useSelector(state => state.addMovieReducer);
	const genres = getGenresListExceptFirst();
	const schema = {
		title: Joi.string().required().label("Title"),
		genre: Joi.string().required().label("Genre"),
		numberInStock: Joi.number()
			.positive()
			.integer()
			.required()
			.label("NumberInStock"),
		dailyRentalRate: Joi.number()
			.positive()
			.required()
			.label("DailyRentalRate"),
	};

	const { editing } = movie_state;
	// let [isBlocking, setIsBlocking] = useState(false);

	// ANCHOR METHODS

	const validate = () => {
		const errors = {};
		const { data } = movie_state;

		//#region [rgba (55, 100, 100, 0.2)] validate data using joi
		var { error } = Joi.validate(data, schema, {
			abortEarly: false,
		});

		// if null, return null
		// if not null, then there is an error
		if (!error) return null;

		for (let i of error.details) errors[i.path[0]] = i.message;
		return errors;
		//#endregion

		//#region [rgba (55, 100, 0, 0.5)]
		// Old Logic
		// if (data.username.trim() === "") {
		// 	errors.username = "Username is required";
		// }
		// if (data.password.trim() === "") {
		// 	errors.password = "Password is required";
		// }
		// return Object.keys(errors).length === 0 ? {} : errors;
		//#endregion
	};

	const validateProperty = ({ name, value }) => {
		// currentTarget.name and currentTarget.value
		// old schema
		const obj = { [name]: value };
		const schema_to_validate = { [name]: schema[name] };
		const { error } = Joi.validate(obj, schema_to_validate);

		return error ? error.details[0].message : null;
	};

	const onOptionChange = (type, value) => {
		const new_data = { [type]: value };
		dispatch(setData(new_data));
		dispatch(setSubmitDisabled({ submit_disabled: false }));
	};

	// destructure the event object and rename its properties
	const onChange = ({ currentTarget: input }) => {
		// check for errors in current field
		const errors = {};
		const errorMessage = validateProperty(input);
		if (errorMessage) {
			errors[input.name] = errorMessage;
			dispatch(setError(errors));
		} else dispatch(deleteError(input.name));

		// set username and password to the  as they are typed
		const finalData = {};
		finalData[input.name] = input.value;
		dispatch(setData(finalData));

		if (errors === {}) dispatch(setSubmitDisabled({ disabled: false }));
	};

	const renderInput = (name, label, autoFocus, type = "text") => {
		const { data } = movie_state;

		return (
			<div>
				<InputGroup
					name={name}
					type={type}
					labelValue={label}
					autoFocus={autoFocus}
					defaultValue={data[name]}
					onChange={onChange}
					error={movie_state.errors[name]}
					// ref={this.username}
				/>
				{name === "email" ? (
					<small id="emailHelp" className="form-text text-muted">
						We'll never share your email with anyone else.
					</small>
				) : null}
			</div>
		);
	};

	const renderSelectOptionInputGroup = (name, options, defaultValue) => {
		return (
			<SelectOptionInputGroup
				name={name}
				options={options}
				defaultValue={defaultValue}
				onChange={({ target: { value } }) => onOptionChange(name, value)}
			/>
		);
	};

	const renderButton = label => {
		return (
			<button
				disabled={validate()}
				className="btn btn-primary text-center"
				type="submit"
			>
				{label}
			</button>
		);
	};

	const handleSubmit = e => {
		e.preventDefault();
		const errors = validate();
		if (errors) {
			dispatch(setError(errors));
			return;
		}

		doSubmit();
	};

	const doSubmit = () => {
		// call the server
		dispatch(updateMovie({ _id, ...movie_state.data }));
		dispatch(resetPreviousMovieData());
		dispatch(setEditingFalse());
		navigate("/movies");
	};

	// usePrompt(
	// 	"Hello from usePrompt -- Are you sure you want to leave?",
	// 	isBlocking
	// );

	return (
		<div className="container">
			<div className="row">
				<div className="col-md-6 offset-md-0">
					<form onSubmit={handleSubmit}>
						<h1>Add a movie here</h1>
						{renderInput("title", "Title", "text", true)}
						{renderInput("numberInStock", "Number in Stock", "number")}
						{renderInput("dailyRentalRate", "Rate")}
						{renderSelectOptionInputGroup("genre", genres, genre)}
						{renderButton("Save")}
					</form>
				</div>
			</div>
		</div>
	);
};

export default EditMovieForm;
