import { Component } from "react";
import Joi from "joi-browser";
import InputGroup from "./InputGroup";
import SelectOptionInputGroup from "./SelectOptionInputGroup";

class Form extends Component {
	validate = () => {
		const errors = {};
		// if (type === "text-input") return this.validateTextInput(errors);
		const { data } = this.state;

		//#region [rgba (55, 100, 100, 0.2)] validate data using joi
		var { error } = Joi.validate(data, this.schema, {
			abortEarly: false,
		});

		// if null, return null
		// if not null, then there is an error
		if (!error) return null;

		for (let i of error.details) errors[i.path[0]] = i.message;
		return errors;
		//#endregion

		//#region [rgba (55, 100, 0, 0.5)]
		// Old Logic
		// if (data.username.trim() === "") {
		// 	errors.username = "Username is required";
		// }
		// if (data.password.trim() === "") {
		// 	errors.password = "Password is required";
		// }
		// return Object.keys(errors).length === 0 ? {} : errors;
		//#endregion
	};

	validateProperty = ({ name, value }) => {
		// currentTarget.name and currentTarget.value
		// old schema
		if (name === "") {
			name = "genre";
		}
		const obj = { [name]: value };
		const schema = { [name]: this.schema[name] };
		const { error } = Joi.validate(obj, schema);

		return error ? error.details[0].message : null;

		//#region [rgba (55, 100, 0, 0.5)]
		// if (name === "username") {
		// 	if (value.trim() === "") {
		// 		return "Username is required";
		// 	}
		// }
		// if (name === "password") {
		// 	if (value.trim() === "") {
		// 		return "Password is required";
		// 	}
		// }
		//#endregion
	};

	onOptionChange = (type, value) => {
		const data = { ...this.state.data };
		data[type] = value;
		this.setState({ data, disabled: false });
	};

	// destructure the event object and rename its properties
	onChange = ({ currentTarget: input }) => {
		// check for errors in current field
		const errors = { ...this.state.errors };
		const errorMessage = this.validateProperty(input);
		if (errorMessage) errors[input.name] = errorMessage;
		else delete errors[input.name];

		// set username and password to the state as they are typed
		const data = { ...this.state.data };
		data[input.name] = input.value;
		this.setState({ data, errors });
		if (this.state.errors === {}) this.setState({ disabled: false });
	};

	renderInput(name, label, type = "text") {
		const { data } = this.state;

		return (
			<div>
				<InputGroup
					name={name}
					type={type}
					labelValue={label}
					defaultValue={data[name]}
					onChange={this.onChange}
					error={this.state.errors[name]}
					// ref={this.username}
				/>
				{name === "email" ? (
					<small id="emailHelp" className="form-text text-muted">
						We'll never share your email with anyone else.
					</small>
				) : null}
			</div>
		);
	}

	renderSelectOptionInputGroup(name, options, defaultValue) {
		return (
			<SelectOptionInputGroup
				name={name}
				options={options}
				defaultValue={defaultValue}
				onChange={({ target: { value } }) => this.onOptionChange(name, value)}
			/>
		);
	}

	renderButton(label) {
		return (
			<button
				disabled={this.validate()}
				className="btn btn-primary text-center"
				type="submit"
			>
				{label}
			</button>
		);
	}
}

export default Form;
